/**
 * @typedef {import("../types")} CTypes
 */

/**
 *
 * @param gearName {string}
 * @return {*}
 */
export function getGearIcon(gearName) {
  return require('assets/img/eq_images/' + gearName + '.png');
}

/**
 *
 * @param gearName {string}
 * @return {CTypes.GearJson}
 */
export function getGearJson(gearName) {
  return require('assets/json/eq_json/' + gearName + '.json');
}

/**
 *
 * @param gearJson {GearJson}
 * @param gearName {string}
 * @return {string}
 */
export function getGearDisplayName(gearJson, gearName) {
  try {
    return gearJson.ENName && gearJson.ENName !== '' ? gearJson.ENName : gearJson.Name;
  } catch (e) {
    console.warn(`Failed to get gear display name for '${gearName}'. Falling back to gearName.`);
    return gearName;
  }
}

/**
 *
 * @param gearJson {CTypes.GearJson|undefined}
 * @param gearName {string}
 * @return {string}
 */
export function getGearAcquisition(gearJson, gearName) {
  try {
    const gearLoc = gearJson.DropLocation;
    const labFrom = gearJson.LabFrom;

    if (gearLoc == null && labFrom != null) {
      return "Gear Lab";
    }

    // remove markdown links
    let result = gearLoc.replaceAll(/\[\[([^|]*?\|)*?([^|]*?)]]/g, "$2")
    if (labFrom != null) {
      if (result.endsWith(".")) {
        result += " Can also be crafted in Gear Lab.";
      } else {
        result += ", Gear Lab";
      }
    }

    return result;
  } catch (e) {
    console.warn(`Failed to get gear acquisition for '${gearName}'.`);
    return '-';
  }
}

/**
 *
 * @param gearJson {CTypes.GearJson|undefined}
 * @param gearName {string}
 */
export function openGearInWiki(gearJson, gearName) {
  try {
    const disambiguator = gearJson.disambiguator ? `#tabber-tabpanel-${gearJson.disambiguator}` : "";
    const url = `https://azurlane.koumakan.jp/wiki/${gearJson.Name.replace(/\s/g, "_")}${disambiguator}`;
    window.open(url, "_blank");
  } catch (e) {
    alert(`Unable to find the link for '${gearName}'! :(`);
  }
}

/**
 *
 * @param gearJson {CTypes.GearJson|undefined}
 * @param gearName {string}
 * @return {string}
 */
export function getGearRarityCSSClass(gearJson, gearName) {
  try {
    switch (gearJson.Stars + '') {
      case "2":
        return "rarity-normal";
      case "3":
        return "rarity-rare";
      case "4":
        return "rarity-elite";
      case "5":
        return  "rarity-super-rare";
      case "6":
        return "rarity-ultra-rare";
      default:
        return "rarity-normal";
    }
  } catch (e) {
    console.warn(`Failed to get rarity class for '${gearName}'.`);
    return "rarity-normal";
  }
}