/**
 * @import CTypes from "../../types"
 */

import { Col } from "reactstrap";
import ShipRoleIcons from './ShipRoleIcons';
import Image from '../Common/Image';

export default function Ship(props) {

  /** @type {Ship} */
  const ship = props.ship;

  /** @type {CTypes.RoleTypes} */
  const RoleTypes = props.RoleTypes;

  const openModal = () => {
    /** @type {CTypes.OpenShipModalFn} */
    const openShipModal = props.openShipModal;

    openShipModal(ship);
  };

  if (ship === undefined) {
    return null;
  }

  // this image needs the same dimensions as the max size that the image can be


  return (
    <Col xs="4" sm="4" md="2">
      <ShipRoleIcons shipInfo={ship.getShipInfo()} roleTypes={RoleTypes} />
      <div
        style={{'display': 'inline-block', 'cursor': 'pointer'}}
        className={"rounded img-raised ship-rarity-padding " + ship.getRarityClass()}
        onClick={() => openModal()}
      >
        <Image
          src={ship.getImage()}
          alt={ship.getDisplayName()}
          style={{'maxHeight': '116px'}}
        ></Image>
      </div>
      <p
        className="ship-name-display"
      >
        {ship.getDisplayName()}
      </p>
    </Col>
  );
}
