import changelogsData from '../assets/json/changelog.json';

/**
 * @returns {Array<{date: string, log_list: string[]}>}
 */
export function getChangelog() {
  return changelogsData;
}

/**
 * @returns {string}
 */
export function getLatestChangelogDate() {
    const data = getChangelog();
    if (data.length > 0) {
        return data[0].date;
    } else {
        return "N/A";
    }
}