/**
 * @import CTypes from "../../types"
 */

import { EctlCommon } from "components/Common/EctlCommon";
import { Component, useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { Col, Tooltip } from "reactstrap";
import {
  getGearAcquisition,
  getGearDisplayName,
  getGearIcon, getGearJson,
  getGearRarityCSSClass,
  openGearInWiki,
} from '../../libs/GearHelpers';
import { useThemeStore } from '../../libs/stores';
import Image from '../Common/Image';

/** @type {CTypes.Augments} */
const augment_map = require('../../assets/json/gear/augments.json');

/**
 *
 * @param props {CTypes.EquipItemProps}
 * @return {JSX.Element}
 */
function EqItem(props) {
  const theme = useThemeStore((state) => state.isDarkTheme());
  const gearName = props.item.gear;
  const isActive = props.itemIndex === props.activeItem;
  const isBackgroundItem = props.itemIndex === (props.activeItem + 1) % props.itemCount;
  const zIndex = isActive ? 10000 : (isBackgroundItem ? 9000 : 8000);

  const [tooltipOpen, setTooltipOpen] = useState(false);
  const mainDiv = useRef(null)

  const touch = matchMedia('(hover: none)').matches;
  // console.log(touch);

  const animationTime = 300;
  const style = {
    'position': 'absolute',
    'zIndex': zIndex,
    'width': '100%',
    'height': '100%',
    'top': ((isActive ? 0 :  15.0) + '%'),
    'left': ((isActive ? 0 : 25.0) + '%'),
    'userSelect': 'none',
    'transition': `position ${animationTime}ms, top ${animationTime}ms, left ${animationTime}ms ease, filter ${animationTime}ms ease`,
    'filter': 'brightness(' + (isActive ? 100 : (theme ? 80 : 95)) + '%)',
  };

  /** @type {CTypes.GearJson|undefined} */
  const gearJson = useMemo(() => {
    try {
      return getGearJson(gearName);
    } catch (e) {
      console.warn(`Failed to get gear json for '${gearName}'.`);
      return undefined;
    }
  }, [gearName]);

  /** @type {CTypes.TooltipFunc} */
  const setTooltipState = useCallback((open) => {
    if (open && !isActive) {
      // console.log("Tooltip open on inactive item", props.item.gear);
      return;
    }
    setTooltipOpen(open);
  }, [isActive]);

  /** @type {(e: React.MouseEvent) => void} */
  const toggleTooltip = (_e) => {
    // console.log(e);
    if (touch) {
      return;
    }

    setTooltipState(!tooltipOpen);
  };

  const handleClick = (e) => {
    e.preventDefault();
    e.stopPropagation();

    if (props.animationLock) {
      return;
    }

    props.tooltipHelper.closeAllTooltips();
    if (isActive) {
      props.setWikiLock(false);
      setTooltipState(!tooltipOpen);
    } else {
      props.setWikiLock(true);
      props.cycleItems();
    }
  }

  const handleDoubleClick = (e) => {
    e.preventDefault();
    e.stopPropagation();

    if (props.animationLock) {
      return;
    }

    if (isActive && !props.wikiLock) {
      props.tooltipHelper.closeAllTooltips();
      openGearInWiki(gearJson, gearName);
    }
  }

  useEffect(() => {
    props.tooltipHelper.allTooltipFuncs.push(setTooltipState);

    return () => {
      props.tooltipHelper.allTooltipFuncs = props.tooltipHelper.allTooltipFuncs.filter((func) => func !== setTooltipState);
    };
  }, [props.tooltipHelper, setTooltipState]);

  useEffect(() => {
    // The idea here is that if the user follows the animated element with the mouse,
    // the tooltip won't open itself, since the mouse never leaves and reenters the element.
    // So we wait until the animation is done, then open the tooltip if the mouse is still over the element

    if (isActive && !props.animationLock && !touch && mainDiv.current?.matches(':hover')) {
      // console.log('special open');
      props.tooltipHelper.closeAllTooltips();
      setTooltipState(true);
    }

  }, [props.animationLock, props.tooltipHelper, isActive, mainDiv, setTooltipState, touch]);

  const id = "_" + props.slotNumber + "_" + props.index + "_" + props.itemIndex;

  return (<>
    <div
      style={style}
      id={id}
      onClick={handleClick}
      onDoubleClick={handleDoubleClick}
      ref={mainDiv}
    >
      <Image
        alt="..."
        className={
          'rounded eq-rarity-padding ' +
          getGearRarityCSSClass(gearJson, gearName) + (isActive ? ' img-raised' : '')
        }
        src={getGearIcon(gearName)}
        style={{
          maxWidth: '128px',
          width: '100%',
          cursor: 'pointer',
          aspectRatio: 1,
          objectFit: 'contain',
        }}
      ></Image>

      {props.itemCount > 1 ? (
        <div style={{
          'position': 'absolute',
          'bottom': 0,
          'right': 0,
          'cursor': 'pointer',
        }}>
          <svg style={{ 'pointerEvents': 'none', 'width': '100%', 'height': '100%' }} viewBox="0 0 100 100">
            <text style={{
              'color': '#0F0F0F',
              'fontWeight': '500',
              'fontFamily': 'Cabin Condensed',
              'textAnchor': 'end',
              'fontSize': '1.0rem',
              'userSelect': 'none',
              'WebkitUserSelect': 'none',
            }} x="94" y="93">
              {(props.itemIndex + 1) + '/' + (props.itemCount)}
            </text>
          </svg>
        </div>
      ) : (<></>)}
      <Tooltip
        delay={0}
        placement="auto"
        className="popover-default"
        target={id}
        toggle={toggleTooltip}
        isOpen={tooltipOpen}
      >
        <div className="ectl-text-black">
          <h6 className="ectl-text-black">{getGearDisplayName(gearJson, gearName)}</h6>
          <hr/>
          {props.item.tooltip}
          <hr/>
          {getGearAcquisition(gearJson, gearName)}
        </div>
      </Tooltip>
    </div>
  </>);
}

/**
 * @param props {CTypes.EquipEntryProps}
 * @return {JSX.Element}
 */
function EqEntry(props) {
  const [activeItem, setActiveItem] = useState(0);
  // this stops gear cycling from counting towards the wiki double click
  const [wikiLock, setWikiLock] = useState(false);
  // this stops any clicks and tooltips from showing during the cycling animation
  const [animationLock, setAnimationLock] = useState(false);

  const cycleItems = () => {
    setAnimationLock(true);

    setActiveItem((activeItem + 1) % props.items.length);

    setTimeout(() => {
      setAnimationLock(false);
    }, 300);
  };

  return (
    <div
      key={props.index}
      style={{ 'position': 'relative', 'zIndex': 10000, 'width': '100%', 'aspectRatio': 1 }}
    >
      {props.items.map((item, index) => {
        return (<EqItem
          item={item}
          index={props.index}
          itemIndex={index}
          activeItem={activeItem}
          tooltipHelper={props.tooltipHelper}
          cycleItems={cycleItems}
          slotNumber={props.slotNumber}
          itemCount={props.items.length}
          wikiLock={wikiLock}
          setWikiLock={setWikiLock}
          animationLock={animationLock}
          key={index}
        />);
      })}
    </div>
  );
}

export default class EquipmentColumn extends Component {
  /** @type {EquipItem[]} */
  eqSlot;
  /** @type {CTypes.EquipTooltipHelper} */
  tooltipHelper;

  constructor(props) {
    super(props);

    /** @type {number} */
    this.slotNumber = props.slotNumber;
    /** @type {boolean} */
    this.isKai = props.state.isKai;
    /** @type {CTypes.ShipJson} */
    this.shipJson = props.state.shipJson;
    /** @type {string} */
    this.shipName = props.shipName;

    if (this.slotNumber === 6) {
      this.eqType = "Augment";
      let itemArray = augment_map.templates[EctlCommon.getSimplifiedHullCode(EctlCommon.getHullCodeFromStr(this.shipJson.Type))];

      for (const over in augment_map.overrides) {
        // console.log(this.shipName);
        if (augment_map.overrides[over].includes(this.shipName)) {
          itemArray = augment_map.templates[over];
        }
      }

      if (augment_map.unique[this.shipName]) {
        itemArray = [augment_map.unique[this.shipName]].concat(itemArray);
      }

      this.eqSlot = itemArray;
    } else {

      this.eqSlot = props.state.eqData["slot_" + this.slotNumber];
      this.eqType = this.shipJson["Eq" + this.slotNumber + "Type"];
    }

    if (!this.eqType){
      this.eqType = "Auxiliary " + (this.slotNumber - 3);
    }

    this.tooltipHelper = props.tooltipHelper;
  }

  hasMultipleItemsInLastEntry(){
    if (this.eqSlot.length === 0){
      return false;
    }
    const lastEntry = this.eqSlot[this.eqSlot.length - 1];
    if (Array.isArray(lastEntry)){
      return lastEntry.length > 1;
    }
    return false;
  }

  render(){
    const getEqType = () => {
      if (!this.eqType) {
        return <span>&nbsp;</span>;
      }

     return <span>{this.eqType.split("/").join(" / ")}</span>;
    }

    const getEqEff = () => {
      if (!this.eqType || /^Auxiliar/.exec(this.eqType) || /^Augment/.exec(this.eqType)){
        return <span>&nbsp;</span>;
      }

      let base = this.shipJson["Eq" + this.slotNumber + "BaseMax"];
      let eff = this.shipJson["Eq" + this.slotNumber + "EffInitMax"];

      if (this.isKai) {
        // If the ship has a retrofit and the base number does not change, then "BaseKai" is not defined,
        // so we use the base number from the non-retrofit version.
        // This might not be the same for eff, but I added it just in case.
        base = this.shipJson["Eq" + this.slotNumber + "BaseKai"] ?? base;
        eff = this.shipJson["Eq" + this.slotNumber + "EffInitKai"] ?? eff;
      }

      return <span>{base}  {eff} eff</span>;
    }

    return (<>
      <h6>{getEqType()}</h6>
      <h6>{getEqEff()}</h6>
      <Col style={{ marginBottom: this.hasMultipleItemsInLastEntry() ? '15%' : 0, gap: '2px', display: 'flex', flexDirection: 'column' }}>
        {this.eqSlot.map((item, index) => {
          /** @type {FlatEquipItem[]} */
          const itemArray = Array.isArray(item) ? item : [item];

          return (
            <EqEntry
              items={itemArray}
              index={index}
              tooltipHelper={this.tooltipHelper}
              slotNumber={this.slotNumber}
              key={index}
            />
          );
        })}
      </Col>
    </>);
  }
}
