/**
 * @import {Ship} from "../../libs/stores"
 */

import { Button, Modal, ModalBody } from 'reactstrap';
import EquipmentSection from './EquipmentSection';
import ShipDescriptionSection from './ShipDescriptionSection';
import { useEffect, useState } from 'react';
import Image from '../Common/Image';

export default function ShipModal(props) {
  /** @type {Ship} */
  const ship = props.ship;

  /** @type {boolean} */
  const isOpen = props.isOpen;
  /** @type {() => void} */
  const close = props.close;

  const [linkCopied, setLinkCopied] = useState(false);

  useEffect(() => {
    setLinkCopied(false);
  }, [isOpen]);

  return <Modal size="lg" isOpen={isOpen} toggle={() => close()}>
    {ship === undefined ? 'No ship data set' :
      <div className="ectl-primary">
        <div className="modal-header justify-content-center">
          <button
            className="close"
            type="button"
            onClick={() => close()}
          >
            <i className="now-ui-icons ui-1_simple-remove"></i>
          </button>
          <div style={{ textAlign: 'center' }}>
            <Image
              alt="..."
              className={'rounded img-raised ship-rarity-padding ' + ship.getRarityClass()}
              src={ship.getBannerImage()}
            ></Image>
            <h2
              className="title hover-link ectl-text ectl-title"
              style={{ cursor: 'pointer' }}
              onClick={(e) => {
                window.open(ship.getWikiLink(), '_blank');
              }}
            >
              {ship.getDisplayName()}
            </h2>
          </div>
        </div>
        <ModalBody>
          <hr style={{ marginTop: 0 }}/>
          <EquipmentSection
            shipName={ship.name}
            rarity={ship.getRarityClass()}
          />
          <hr/>

          {ship.getShipInfo().reviews.map((item, index) => (
            <>
              <ShipDescriptionSection
                key={index}
                title={item.reviewer}
                startCollapsed={index !== 0}
              >
                <p>
                  {item.review}
                </p>
              </ShipDescriptionSection>
              <hr key={'hr' + index}/>
            </>
          ))}

          <div className="ship-modal-footer">
            <Button
              onClick={() => {
                close();
              }}
            >
              Close
            </Button>
            <Button
              onClick={() => {
                window.open(ship.getWikiLink(), '_blank');
              }}
            >
              View on wiki
            </Button>
            <Button
              onClick={() => {
                void navigator.clipboard.writeText(window.location.href);
                setLinkCopied(true);
              }}
            >
              {linkCopied ? 'Link copied!' : 'Share (Copy Link)'}
            </Button>
          </div>
        </ModalBody>
      </div>
    }
  </Modal>
}