import { useEffect, useMemo, useState } from 'react';
import { DEFAULT_IMAGE } from '../../libs/Helpers';
import { useImageStore } from '../../libs/stores';

/**
 * An image component that shows a loading image and caches the image data.
 *
 * @param props {{src: string, alt?: string, style?: Record<CSSProperties>, className?: string}}
 * @return {JSX.Element}
 * @constructor
 */
export default function Image(props) {
  const getImage = useImageStore(state => state.getImage)

  const src = useMemo(() => props.src, [props.src]);

  const [loadedImage, setLoadedImage] = useState(/** @type {string|undefined} */ undefined);

  useEffect(() => {
    let active = true;

    getImage(src, (value) => {
      // only update the image if the component is still active, this is to prevent race conditions
      if (active) {
        setLoadedImage(value);
      }
    });

    return () => {
      active = false;
    };
  }, [getImage, src, setLoadedImage]);

  return <img
    alt={props.alt}
    src={loadedImage ?? DEFAULT_IMAGE}
    style={props.style}
    className={props.className}
  />
}