/**
 * @import CTypes from "../../types"
 */

import { Col, Row } from 'reactstrap';

/**
 * @param props {CTypes.ShipRoleIconsProps}
 * @returns {JSX.Element}
 */
export default function ShipRoleIcons(props) {
  /** @type {JSX.Element[]} */
  const shipIconList = [];

  for (const roleGroup of props.shipInfo.roles){
    roleGroup.sort((a, b) => props.roleTypes[a].sort_index - props.roleTypes[b].sort_index);
    for (const role of roleGroup){
      shipIconList.push(
        <img
          key={roleGroup + '_' + role}
          alt="..."
          data-tip={props.roleTypes[role].tip}
          style={{ 'maxHeight': '22px', 'display': 'block', 'paddingBottom': '2px'}}
          src={require("assets/img/icons/" + props.roleTypes[role].img)}
          data-tooltip-content={props.roleTypes[role].tip}
          data-tooltip-id="ship-icon-tooltip"
        />
      );
    }
    shipIconList.push(
      <span key={"/" + roleGroup} style={{"color": 'var(--ectl-text-muted)', "maxHeight": "20px", "fontWeight": "700"}}>&nbsp;/&nbsp;</span>
    );
  }
  shipIconList.pop();

  return (
    <Row style={{ 'minHeight': '22px', display: props.inline ? 'inline-flex' : 'flex' }}>
      <Col style={{ 'whiteSpace': 'nowrap' }}>
        <div style={{ 'display': 'flex', 'justifyContent': 'center'}}>
          {shipIconList}
        </div>
      </Col>
    </Row>
  );
};