/**
 * @import CTypes from "../../types"
 */

import { useState } from 'react';

/**
 *
 * @param props {CTypes.ShipDescriptionSectionProps}
 * @return {JSX.Element}
 */
export default function ShipDescriptionSection(props) {
  const [collapsed, setCollapsed] = useState(props.startCollapsed ?? true);

  const style = props.centered ? { justifyContent: "center", flexDirection: 'row-reverse' } : {};

  return (
    <div>
      <h5
        className={"ectl-text ectl-expandable " + (collapsed ? "collapsed " : "")}
        style={style}
        onClick={() => setCollapsed(!collapsed)}
      >
        <i></i>
        {props.title}
      </h5>
      <div>
        {props.children}
      </div>
    </div>
  )
}