import { NavItem, NavLink } from "reactstrap";
import { useNavigate } from 'react-router';
import { useShipStore } from '../../libs/stores';

export default function HullTabs(props) {
  const navigate = useNavigate();

  const clearFilters = useShipStore(state => state.clearFilters);
  const setActiveTab = useShipStore(state => state.setActiveTab);
  const activeTab = useShipStore(state => state.activeTab);
  const hasShips = useShipStore(state => state.tabHasShips(props.index));
  const search = useShipStore(state => state.search);

  /** @type {number} */
  const index = props.index;
  /** @type {string} */
  const text = props.text;

  const tabNavTarget = "/main/" + (text.toLowerCase().split("/")[0] || "pablo");
  const tabHref = "#" + tabNavTarget;

  return (
    <NavItem sm="6" md="2">
      <NavLink
        className={(activeTab === index ? "active " : "") + (search && hasShips ? "ectl-nav-pill-has-ships " : "") + "ectl-nav-pill"}
        href={tabHref}
        onClick={(e) => {
          e.preventDefault();
          setActiveTab(index);
          clearFilters();
          navigate(tabNavTarget);
        }}
      >
        {text}
      </NavLink>
    </NavItem>
  );
}
