/*eslint-disable*/
import { createRef, useEffect } from 'react';
import { Container } from "reactstrap";
import Switch from "react-bootstrap-switch";
import Countdown from "react-countdown";

import { ConditionalWrapper } from "components/Common/EctlCommon.js";
import { useScrollStore, useThemeStore } from '../../libs/stores';

function CurrentEventHeader(props) {
  /*
   * This data will automatically be ignored if the event is expired.
   */
  const currentEventData = {
    /*
     * Link to the current event wiki page
     */
    "link": "https://azurlane.koumakan.jp/wiki/A_Dance_for_Amahara_Above",
    /*
     * Current event name. Ignored in "No Current Event" mode.
     */
    "name": "Current Event: A Dance for Amahara Above",
    /*
     * Set these to the date listed on the banner
     * They're adjusted to work properly with js later on.
     *
     * If "No Current Event" is desired,
     * set these to some time in the past.
     */
    "bannerDate": {
      "year": 2025,
      "month": 10,
      "day": 2,
    },
  };

  const getEventEndDate = () => {
    const year = currentEventData.bannerDate.year;
    /*
     * js indexes months at 0, not 1
     * so the month here needs to be one less
     * than the actual month
     * e.g. Use 3 for April, not 4
     *
     * TL;DR: Month on banner - 1
     */
    const month = currentEventData.bannerDate.month - 1;
    /*
     * Events end at 11:59:59 on the date listed
     * so in UTC it ends on the day after the banner shows
     * Server time is UTC-7 regardless of DST
     * so events always end at 7:00 UTC
     * 
     * TL;DR: Day on banner + 1
     */
    const day = currentEventData.bannerDate.day + 1;
    /*
     * Leave this at 7:00:00 for all purposes
     * Server time is UTC-7 regardless of DST
     */
    const hour = 7;
    // new Date(foo) uses localtime
    // we workaround this with new Date(Date.UTC(foo))
    return new Date(Date.UTC(year, month, day, hour, 0, 0, 0));
  };

  const currentEventActive = getEventEndDate().getTime() > Date.now();

  // Set the date for maintenance based on the date in announcement tweet
  const MaintDate = {
    "year": 2025,
    "month": 9,
    "day": 12,
  };

  // Set the number of hours scheduled for maintenance here
  const maintenanceHours = 8;

  const getMaintDate = () => {
    const year = MaintDate.year;
    /*
     * js indexes months at 0, not 1
     * so the month here needs to be one less
     * than the actual month
     * e.g. Use 3 for April, not 4
     *
     * TL;DR: Month on banner - 1
     */
    const month = MaintDate.month - 1;
    /*
     * Maint starts at 00:00:00 on the date listed
     * meaning unlike event end date the month displayed in tweet is correct
     * Server time is UTC-7 regardless of DST
     * so events always end at 7:00 UTC
     * 
     * TL;DR: Day = day in tweet
     */
    const day = MaintDate.day;
    /*
     * Leave this at 7:00:00 for all purposes
     * Server time is UTC-7 regardless of DST
     */
    const hour = 7;
    // new Date(foo) uses localtime
    // we workaround this with new Date(Date.UTC(foo))
    return new Date(Date.UTC(year, month, day, hour, 0, 0, 0));
  };

  const maintDate = getMaintDate();
  const maintenanceEndDate = new Date(maintDate.getTime() + maintenanceHours*60*60*1000);
  const maintenanceActive = Date.now() > getMaintDate().getTime() && Date.now() < maintenanceEndDate.getTime();

  const banners = {};
  const importAll = r => {
    r.keys().forEach((key) => (banners[key] = r(key)));
  };
  importAll(require.context("../../assets/img/event_banner/", false, /.*/));
  const bannerImage = banners[Object.getOwnPropertyNames(banners)[0]];
  const maintenanceBannerImage = require("../../assets/img/maintenance_banner.png");
  const expiredImage = require("../../assets/img/no_active_event.png");

  let currentImage;
  if (maintenanceActive) {
    currentImage = maintenanceBannerImage;
  } else if (currentEventActive) {
    currentImage = bannerImage;
  } else {
    currentImage = expiredImage;
  }

  return (<>
    <ConditionalWrapper
      condition={!maintenanceActive && currentEventActive && currentEventData.link}
      wrapper={children => <a target="_blank" href={currentEventData.link}>{children}</a>}
    >
      <img
        alt="Event Banner Image"
        className="banner-image"
        src={currentImage}
      />
    </ConditionalWrapper>
    <h3 className="ectl-text-white ectl-font-weight-normal">
      {maintenanceActive ? <>Server Maintenance</> : currentEventActive ? <>{currentEventData.name}</> : <>No Current Event</>}
    </h3>
    {(maintenanceActive || currentEventActive) && (
      <span className="ectl-text-white">
        <h2>
          <Countdown date={maintenanceActive ? maintenanceEndDate : getEventEndDate()} daysInHours={true} className="ectl-text-white ectl-font-weight-normal" />
        </h2>{" "}
        {maintenanceActive ? <>until maintenance is scheduled to end.</> : <>until event ends.</>}
      </span>
    )}
  </>);
}

function ParallaxBackground() {
  const scrollPosition = useScrollStore((state) => state.scrollPosition);
  const pageHeader = createRef();

  useEffect(() => {
    if (window.innerWidth > 991) {

      let windowScrollTop = scrollPosition / 3;
      if (pageHeader.current !== null) {
        pageHeader.current.style.transform = 'translate3d(0, ' + windowScrollTop + 'px, 0)';
      }
    } else {
      pageHeader.current.style.transform = 'translate3d(0, 0px, 0)';
    }
  }, [scrollPosition]);

  return <div
    className="page-header-image"
    style={{
      backgroundImage: 'url(' + require('assets/img/bg.jpg') + ')',
      filter: 'brightness(0.7)',
    }}
    ref={pageHeader}
  ></div>;
}

export default function IndexHeader() {
  const themeToggle = createRef();
  const isLightTheme = useThemeStore((state) => state.isLightTheme());
  const toggleTheme = useThemeStore((state) => state.toggleTheme);

  return (
    <>
      <div className="page-header clear-filter" filter-color="blue" style={{'maxHeight': '100rem'}}>
        <ParallaxBackground />
        <Container>
          <div className="content-center brand" style={{ top: "50%" }}>
            <img
              alt="..."
              className="n-logo"
              src={require("assets/img/site_icon.png")}
            ></img>
            <h1 className="h1-seo ectl-text-white">Azur Lane's EN Community Tier List</h1>
            <h3 className="ectl-text-white ectl-font-weight-normal">
              Created and supported by #gameplay-help and a bunch of AL veterans
              from various servers
            </h3>
            <h6 className="ectl-text-white">
              Theme&nbsp;&nbsp;&nbsp;
              <Switch
                value={isLightTheme}
                ref={themeToggle}
                offText="Dark"
                onText="Light"
                onChange={(el, state) => {
                  if (state !== isLightTheme) {
                    toggleTheme();
                  }
                }}
              ></Switch>
            </h6>
            <br style={{'height': '300%'}}/>
            <CurrentEventHeader/>
          </div>
        </Container>
      </div>
    </>
  );
}
