/**
 * @import CTypes from "../../types"
 */

import { Component } from "react";
import EquipmentColumn from "./EquipmentColumn.js";
import { getShipInfo, getShipJSON, isShipKai } from '../../libs/ShipHelpers';
/** @type {CTypes.GearMapping} */
import gearMapping from "../../assets/json/gear/GearMapping.json";
/** @type {CTypes.GearTemplates} */
import bbTemplates from "../../assets/json/gear/BBTemplates.json";
/** @type {CTypes.GearTemplates} */
import caTemplates from "../../assets/json/gear/CATemplates.json";
/** @type {CTypes.GearTemplates} */
import clTemplates from "../../assets/json/gear/CLTemplates.json";
/** @type {CTypes.GearTemplates} */
import cvTemplates from "../../assets/json/gear/CVTemplates.json";
/** @type {CTypes.GearTemplates} */
import ddTemplates from "../../assets/json/gear/DDTemplates.json";
/** @type {CTypes.GearTemplates} */
import ssTemplates from "../../assets/json/gear/SSTemplates.json";
/** @type {CTypes.GearTemplates} */
import otherTemplates from "../../assets/json/gear/OtherTemplates.json";
/** @type {CTypes.SlotTemplates} */
import slotTemplates from "../../assets/json/gear/SlotTemplates.json";

/**
 * @type {CTypes.GearTemplates}
 */
const hullTemplates = {
  ...bbTemplates,
  ...caTemplates,
  ...clTemplates,
  ...cvTemplates,
  ...ddTemplates,
  ...ssTemplates,
  ...otherTemplates,
};

export default class EquipmentSection extends Component {
  constructor(props) {
    super(props);

    try {
      /** @type {string} */
      const shipName = this.props.shipName;
      /** @type {CTypes.ShipInfo} */
      const shipInfo = getShipInfo(shipName);

      /** @type {string} */
      const shipRarity = this.props.rarity;
      const slotMap = hullTemplates[gearMapping[shipName]];

      /** @type {CTypes.ShipGearTemplate} */
      const templateForShip = {};
      for (let i = 1; i <= 5; i++){
        templateForShip["slot_" + i] = slotTemplates[slotMap["slot_" + i]];
      }

      /** @type {CTypes.ShipJson} */
      const shipJson = getShipJSON(shipName);

      const isKai = isShipKai(shipInfo, shipJson);

      this.state = {
        eqData: templateForShip,
        shipJson: shipJson,
        isKai: isKai,
        shipRarity: shipRarity,
      };
    } catch (e) {
      console.log(e);
      console.log("Unable to link ship with equipment.");
    }

    /** @type {CTypes.EquipTooltipHelper} */
    const tooltipHelper = {
      allTooltipFuncs: [],
      closeAllTooltips: () => {
        for (const f of tooltipHelper.allTooltipFuncs){
          f(false);
        }
      }
    };
    this.tooltipHelper = tooltipHelper;
  }

  render() {

    if (!this.state) {
      return '';
    }

    return <>
      <div onClick={this.tooltipHelper.closeAllTooltips}>
        <div className={'equipment-grid'}>
          {[1, 2, 3, 4, 5, 6].map((i) => (
            <div className={'equipment-column'} key={i}>
              <EquipmentColumn
                slotNumber={i}
                state={this.state}
                tooltipHelper={this.tooltipHelper}
                shipName={this.props.shipName}
              />
            </div>
          ))}
        </div>
      </div>

      <span
        style={{ display: 'block', textAlign: 'center', width: '100%', marginTop: '15px' }}
        className="ectl-text-muted"
      >
        Double-click an equipment icon to open its wiki page.<br/>
        Click the next equipment in an equipment set to cycle through the list.<br/>
        Mouse over an equipment icon to see a description (or tap, on mobile).
      </span>
    </>;
  }
}
