/**
 * @import CTypes from "../../types"
 */

import Ship from "./Ship.js";
import { Row } from "reactstrap";
import { Tooltip } from "react-tooltip";
import { useShipStore } from '../../libs/stores';


export default function TierPane(props) {

  const activeTab = useShipStore(state => state.activeTab);
  const ships = useShipStore(state => state.ships);
  const filteredShipTiers = useShipStore(state => state.filteredShipTiers);

  /** @type {string[]} */
  const TierNames = props.TierNames;
  /** @type {CTypes.RoleTypes} */
  const RoleTypes = props.RoleTypes;
  /** @type {CTypes.HullTypes} */
  const HullTypes = props.HullTypes;
  /** @type {CTypes.OpenShipModalFn} */
  const openShipModal = props.openShipModal;

  const shipHull = HullTypes[activeTab];

  /** @type {(tierArray: string[]) => JSX.Element[]} */
  const getTierRowData = (tierArray) => {
    return tierArray
      .map((shipName, idx) => {
        return (
          <Ship
            key={idx}
            ship={ships[shipName]}
            RoleTypes={RoleTypes}
            openShipModal={openShipModal}
          />
        );
      });
  }

  const hasResults = filteredShipTiers[shipHull].some(x => x.length > 0);

  return (
    <div>
      <hr/>
      <h6 className="ectl-text-muted text-center">
        Hover (or click, on mobile) on the icons above a ship to learn about the conditions they're tiered under. <br/>
        Click on a ship to view more details!
      </h6>
      {!hasResults && <h5 className="title ectl-text text-center">There are no ship girls matching your criteria :(</h5>}
      {filteredShipTiers[shipHull].map((tierArray, idx) => {
        const tierRowData = getTierRowData(tierArray);

        if (tierRowData.length === 0) {
          return <div key={idx}></div>;
        }

        return (
          <div key={idx} className="text-center">
            <h3 className="title ectl-text">
              {TierNames[idx]}
            </h3>
            <Row>
              {tierRowData}
            </Row>
          </div>
        );
      })}
      <Tooltip id="ship-icon-tooltip" style={{ maxWidth: '300px' }}/>
      <Tooltip id="ship-filter-tooltip" style={{ maxWidth: '300px' }}/>
    </div>
  );
}
