/**
 * @import CTypes from "../../types"
 */

import {
  Input,
  InputGroup, Button,
} from 'reactstrap';
import { useShipStore } from '../../libs/stores';
import { useMemo } from 'react';
import ShipDescriptionSection from './ShipDescriptionSection';

export default function FilterBar(props) {
  const filters = useShipStore(state => state.filters);
  const setFilter = useShipStore(state => state.setFilter);
  const clearFilters = useShipStore(state => state.clearFilters);

  const search = useShipStore(state => state.search);
  const setSearch = useShipStore(state => state.setSearch);
  const clearSearch = useShipStore(state => state.clearSearch);

  const getFiltersAffectingHullType = useShipStore(state => state.getFiltersAffectingHullType)
  const hullType = props.hullType;

  const filterDefinitions = useMemo(() => {
    return getFiltersAffectingHullType(hullType)
  }, [getFiltersAffectingHullType, hullType]);

  const getFilterOptions = () => {
    /** @type {JSX.Element[]} */
    const filterItemList = [];

    for (const filterDefinition of filterDefinitions) {
      const active = filters.includes(filterDefinition.label);

      filterItemList.push(
        <div
          key={filterDefinition.label}
          style={{ padding: '10px', cursor: 'pointer', border: `1px solid ${active ? 'var(--ectl-muted)' : 'var(--ectl-very-muted)'}`, borderRadius: '10px', backgroundColor: active ? 'var(--ectl-inverse)' : 'var(--ectl-primary)'}}
          onClick={() => setFilter(filterDefinition.label, !active)}
        >
          <p className={`${active ? 'ectl-text-inverse' : 'ectl-text'} ectl-font-weight-bold text-center`}>{filterDefinition.label}</p>
          <span className={`${active ? 'ectl-text-inverse' : 'ectl-text'}`}>
            {filterDefinition.description}
          </span>
        </div>,
      );

    }
    return filterItemList;
  };

  return (
    <>
      <InputGroup>
        <Input
          id={"search"}
          name="search"
          placeholder="Search for a Ship"
          type="search"
          className={"search-bar"}
          value={search}
          onChange={(e) => setSearch(e.target.value)}
        />
        <Button className="form-control filter-clear-button" onClick={() => {clearFilters(); clearSearch();}}>
          Reset Filters
        </Button>
      </InputGroup>
      <ShipDescriptionSection title={'Ship Filters'} centered={true} startCollapsed={true}>
        <div className="filter-grid">
          {getFilterOptions()}
        </div>
      </ShipDescriptionSection>
    </>
  )
}

